<template>
  <div>
    <van-nav-bar :title="title" :left-text="showBack" :left-arrow="showArrow" fixed/>
    <form action="javascript:void(0)" method="post">
      <van-cell-group>
        <van-field v-model="form.username" border placeholder="请输入用户名" label="用户名" required/>
        <van-field
          v-model="form.password"
          border
          placeholder="请输入密码"
          type="password"
          label="密 码"
          required
        />
      </van-cell-group>
      <van-button
        type="primary"
        @click="userLogin"
        block
        style="margin-top:10px"
        :loading="loading"
        loading-text="登录中..."
      >登录</van-button>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
import { Field, CellGroup, Toast } from "vant";
import { login } from "@/api/api.js";
export default {
  name: "login",
  components: {
    Field,
    CellGroup
  },
  data() {
    return {
      loading: false,
      form: {
        username: null,
        password: null
      }
    };
  },
  computed: {
    title() {
      return this.$route.meta.title || "";
    },
    showBack() {
      return this.$route.meta.showBack || "";
    },
    showArrow() {
      return this.$route.meta.showArrow || false;
    }
  },
  methods: {
    userLogin() {
      this.loading = true;
      login(this.form)
        .then(res => {
          this.loading = false;
          if (res.code == "success") {
            localStorage.setItem("token", res.message);
            this.$toast.success("登录成功！");
            this.$router.push({
              name: "home"
            });
          }
        })
        .catch(err => {
          this.loading = false;
          localStorage.clear();
          this.$toast.fail(err);
        });
    }
  }
};
</script>

<style scoped>
form {
  position: absolute;
    top:35%;
    transform: translateY(-50%);
    right: 0;
    left: 0;
}
</style>